import axios from "axios";
import { useState } from "react";
import { useEffect } from "react";
import Moviecard from "./Moviecard";
import { Link } from "react-router-dom";
const Newmovie=()=>{
    const [newmovie,setData] = useState([]);
  const getDataFromAPI=()=>{
    const options = {
        method: 'GET',
        url: 'https://movies-api14.p.rapidapi.com/home',
        headers: {
          'X-RapidAPI-Key': 'e97eff6389msh5cc4d1ff8730c0fp131c8cjsnd19142a2fa12',
          'X-RapidAPI-Host': 'movies-api14.p.rapidapi.com'
        }
      };

    axios.request(options).then((opt)=>{
      console.log(opt.data[1].movies);
      setData(opt.data[1].movies);
    }).catch((error)=>{
      console.log( error)
    })

  }
  useEffect(()=>{
    getDataFromAPI();
  },[])
  return (
    <div >
      <div className='container'>
      <h2 class="text-center">New Movie</h2>
        
        <hr/>
        <div className="container">
            <div className="row">
                {newmovie.map((item)=>{
                    const{poster_path, title,_id}=item;
                    return(
                        <div key={_id} className="col-12 col-md-4">
                            <Link to={`/trendingdetails/${_id}`}>
                            <Moviecard poster_path={poster_path} title={title} _id={_id}/>
                            </Link>
                          
                            </div>
                    )
                })}
            </div>
        </div>
      </div>
    </div>
  );
}
export default Newmovie;