import { useParams } from "react-router-dom";
import { useState } from "react";
import { useEffect } from "react";
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import { Link } from "react-router-dom";
import axios from "axios";
const Trendingdetails = () => {
  const { id } = useParams();

 const [trendingdetail, setData] = useState({
    _id: "",
    title: "",
    overview: "",
    youtube_trailer: "",
    poster_path: "",
    genres:"",

    release_date:""

  });
  const [similarMovies, setsimilar] = useState([]);
  const FetchData = () => {
    const options = {
      method: 'GET',
      url: (`https://movies-api14.p.rapidapi.com/movie/${id}`),
      headers: {
        'X-RapidAPI-Key': 'e97eff6389msh5cc4d1ff8730c0fp131c8cjsnd19142a2fa12',
        'X-RapidAPI-Host': 'movies-api14.p.rapidapi.com'
      }
    };

    axios.request(options).then((opt) => {
       console.log(opt.data);
      console.log(opt.data.similarMovies);
      // console.log(opt.data.movie.title);
      // console.log(opt.data.movie.overview);
      // console.log(opt.data.movie.youtube_trailer);
      //console.log(opt.data.movie.similarMovie);
      const data = opt.data.movie
      
      //setData(opt.data.movie);
      setData({
        _id: data._id,
        title: data.title,
        overview: data.overview,
        youtube_trailer: data.youtube_trailer,
        poster_path: data.poster_path,
        genres:data.genres,
        release_date:data.release_date


        


      });
      setsimilar(opt.data.similarMovies)
  

    }).catch((error) => {
      console.log(error)
    })


  }
  const settings = {

    infinite: true,
    autoplay: true,
    speed: 2000,
    autoplaySpeed: 200,
    cssEase: "linear",
    slidesToShow: 4,
    slidesToScroll: 1,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3,
          infinite: true,
          dots: true
        }
      },
      {
        breakpoint: 768,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3
        }
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2
        }
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1
        }
      }
    ]
  };
  useEffect(()=>{
    FetchData();
  },[])
  return (
    <div>
      
   
      <div className="container">
        <div className="row">
          {/* <div className="col-12 col-md-6">
            <img src={trendingdetail.poster_path} alt="" className="img-fluid" />
          </div>
          
          <div className="col-12 col-md-6 text-start detail" >
            <h2>{trendingdetail.title}</h2>
            <h5>Geners: <span>{trendingdetail.genres[0]}</span> <span>{trendingdetail.genres[1]}</span> <span>{trendingdetail.genres[3]}</span></h5>
            <p>{trendingdetail.overview}</p>
            <h5>Release Date: {trendingdetail.release_date}</h5>
            <a href={trendingdetail.youtube_trailer}><button  class="btn btn-dark rounded-pill">Watch Trailer <span><i class="bi bi-youtube"></i></span> </button></a>
          </div> */}
          <div class="card mb-3" >
  <div class="row g-0">
    <div class="col-md-4">
      <img src={trendingdetail.poster_path} class="img-fluid rounded-start" alt="..."/>
    </div>
    <div class="col-md-8">
      <div class="card-body detail">
        <h2 class="card-title">{trendingdetail.title}</h2>
        <h5><button class="btn btn-dark"><span>{trendingdetail.genres[0]}</span></button> <button class="btn btn-dark"><span>{trendingdetail.genres[1]}</span></button>   </h5>
            <p>{trendingdetail.overview}</p>
            <h5>Release Date: {trendingdetail.release_date}</h5>
            <a  href={trendingdetail.youtube_trailer}><button  class=" col-12 btn btn-dark rounded-pill">Watch Trailer <span><i class="bi bi-youtube"></i></span> </button></a>
          
      </div>
    </div>
  </div>
</div>
        </div>
      </div>
      <div className="container">
        <div className="row">
          <div className="col-12 text-center">
           <h2>Similar Movies</h2> 
          </div>
      
          <Slider {...settings}>

{similarMovies.map((item) => {
  const {poster_path,title,_id}=item
  return (
    <div key={_id} className="movie-card">
      
      <Link to={`/trendingdetails/${_id}`}>
      <img src={poster_path} alt="" class="img-fluid"/>
      <h5>{title}</h5>
       </Link>
    </div>

  )
})}
</Slider>
        </div>

      </div>
    </div>
  );
}
export default Trendingdetails;