import image from "./images/movie1.jpg"
import React, { useState, useEffect } from 'react';
import axios from 'axios';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import { Link } from "react-router-dom";

const Home = () => {
  const [movies, setMovies] = useState([]);
  const [newm, setNew] = useState([]);
  const [animation, setAnimation] = useState([])
  const [free, setFree] = useState([]);
  const [family, setFamily] = useState([]);
  const [shows, setShows] = useState([]);
  const [news, setNews] = useState([]);
  const [shot, setShot] = useState([]);
  const [tv, setTv] = useState([]);
  const [docuseries, setDocuseries] = useState([]);

  const getData = () => {
    const options = {
      method: 'GET',
      url: 'https://movies-api14.p.rapidapi.com/home',
      headers: {
        'X-RapidAPI-Key': 'e97eff6389msh5cc4d1ff8730c0fp131c8cjsnd19142a2fa12',
        'X-RapidAPI-Host': 'movies-api14.p.rapidapi.com'
      }
    };
    axios.request(options).then((opt) => {
      // console.log(opt.data[0].movies[0].poster_path);
      // console.log(opt.data[0].movies[0].title);
      setMovies(opt.data[0].movies);
      setNew(opt.data[1].movies);
      setAnimation(opt.data[2].movies);
      setFree(opt.data[3].movies);
      setFamily(opt.data[4].movies);
      setShows(opt.data[5].movies);
      setNews(opt.data[6].movies);
      setShot(opt.data[7].movies);
      setTv(opt.data[8].movies);
      setDocuseries(opt.data[9].movies);
    }).catch((error) => {
      console.log(error)
    })
  }
  useEffect(() => {
    getData();
  }, [])
  const settings = {

    infinite: true,
    autoplay: true,
    speed: 2000,
    autoplaySpeed: 200,
    cssEase: "linear",
    slidesToShow: 4,
    slidesToScroll: 1,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3,
          infinite: true,
          dots: true
        }
      },
      {
        breakpoint: 768,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3
        }
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2
        }
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1
        }
      }
    ]
  };

  return (
    <div class="container-fluid">
      <div class="row">
        <div class="col-12 p-0">
          <div class="card text-bg-dark banner">
            <img src={image} class="card-img h-100" alt="..." />
            <div class="card-img-overlay">
              <h1>Movflx</h1>
              <h2 class="card-title">Unlimited Movies, Tv Shows,  and more.</h2>
            </div>
          </div>
        </div>
        <div class="col-12 ">
          <div class="container-fluid mt-5">
            <div className="movies-slider">
              <h2 class="fw-bold">Trending Movies <Link to="/Trending"><span><i class="bi bi-arrow-right-circle-fill"></i></span></Link></h2>

              <Slider {...settings}>
                {movies.map((item) => {
                  const { poster_path, title, _id } = item
                  return (
                    <div key={_id} className="movie-card">
                      <Link to={`/trendingdetails/${_id}`}>
                        <img src={poster_path} alt="" class="img-fluid" />
                        <h5>{title}</h5>
                      </Link>
                    </div>
                  )
                })}
              </Slider>

            </div>

          </div>

          <div class="container-fluid mt-5">
            <div className="movies-slider">
              <h2 class="fw-bold">New Movies <Link to="/Newmovie"><span><i class="bi bi-arrow-right-circle-fill"></i></span></Link></h2>

              <Slider {...settings}>
                {newm.map((item) => {
                  const { poster_path, title, _id } = item
                  return (
                    <div key={_id} className="movie-card">
                      <Link to={`/trendingdetails/${_id}`}>
                        <img src={poster_path} alt="" class="img-fluid slideimg" />
                        <h5>{title}</h5>
                      </Link>          
                    </div>
                  )
                })}
              </Slider>

            </div>

          </div>
          <div class="container-fluid mt-5">
            <div className="movies-slider">
              <h2 class="fw-bold">Best Animation Movies <Link to="/Animation"><span><i class="bi bi-arrow-right-circle-fill"></i></span></Link></h2>

              <Slider {...settings}>
                {animation.map((item) => {
                  const { poster_path, title, _id } = item
                  return (
                    <div key={_id} className="movie-card">
                      <Link to={`/trendingdetails/${_id}`}>
                        <img src={poster_path} alt="" class="img-fluid slideimg" />
                        <h5>{title}</h5>
                      </Link>
                    </div>

                  )
                })}
              </Slider>

            </div>

          </div>
          <div class="container-fluid mt-5">
            <div className="movies-slider">
              <h2 class="fw-bold">Movies You Can Watch For Free <Link to="/Free"><span><i class="bi bi-arrow-right-circle-fill"></i></span></Link></h2>

              <Slider {...settings}>

                {free.map((item) => {
                  const { poster_path, title, _id } = item
                  return (
                    <div key={_id} className="movie-card">

                      <Link to={`/trendingdetails/${_id}`}>
                        <img src={poster_path} alt="" class="img-fluid slideimg" />
                        <h5>{title}</h5>
                      </Link>
                      {/* <Link to={"/Movie/"+ dt._id}>view More</Link> */}
                      {/* <Link to={"/Movie/"+ dt._id}>view More</Link> */}
                    </div>

                  )
                })}
              </Slider>

            </div>

          </div>
          <div class="container-fluid mt-5">
            <div className="movies-slider">
              <h2 class="fw-bold">Family Time <Link to="/Family"><span><i class="bi bi-arrow-right-circle-fill"></i></span></Link></h2>

              <Slider {...settings}>

                {family.map((item) => {
                  const { poster_path, title, _id } = item
                  return (
                    <div key={_id} className="movie-card">

                      <Link to={`/trendingdetails/${_id}`}>
                        <img src={poster_path} alt="" class="img-fluid slideimg" />
                        <h5>{title}</h5>
                      </Link>
                    </div>

                  )
                })}
              </Slider>

            </div>

          </div>
          <div class="container-fluid mt-5">
            <div className="movies-slider">
              <h2 class="fw-bold">Most Trending Shows <Link to="/Shows"><span><i class="bi bi-arrow-right-circle-fill"></i></span></Link></h2>

              <Slider {...settings}>

                {shows.map((item) => {
                  const { poster_path, title, _id } = item
                  return (
                    <div key={_id} className="movie-card">

                      <Link to={`/trendingdetails/${_id}`}>
                        <img src={poster_path} alt="" class="img-fluid slideimg" />
                        <h5>{title}</h5>
                      </Link>
                    </div>

                  )
                })}
              </Slider>

            </div>

          </div>
          <div class="container-fluid mt-5">
            <div className="movies-slider">
              <h2 class="fw-bold">New Shows <Link to="/Newshow"><span><i class="bi bi-arrow-right-circle-fill"></i></span></Link></h2>

              <Slider {...settings}>

                {news.map((item) => {
                  const { poster_path, title, _id } = item
                  return (
                    <div key={_id} className="movie-card">

                      <Link to={`/trendingdetails/${_id}`}>
                        <img src={poster_path} alt="" class="img-fluid slideimg" />
                        <h5>{title}</h5>
                      </Link>
                      {/* <Link to={"/Movie/"+ dt._id}>view More</Link> */}
                      {/* <Link to={"/Movie/"+ dt._id}>view More</Link> */}
                    </div>

                  )
                })}
              </Slider>

            </div>

          </div>
          <div class="container-fluid mt-5">
            <div className="movies-slider">
              <h2 class="fw-bold">Get a shot of adrenaline <Link to="/shot"><span><i class="bi bi-arrow-right-circle-fill"></i></span></Link></h2>

              <Slider {...settings}>

                {shot.map((item) => {
                  const { poster_path, title, _id } = item
                  return (
                    <div key={_id} className="movie-card">

                      <Link to={`/trendingdetails/${_id}`}>
                        <img src={poster_path} alt="" class="img-fluid slideimg" />
                        <h5>{title}</h5>
                      </Link>

                    </div>

                  )
                })}
              </Slider>

            </div>

          </div>
          <div class="container-fluid mt-5">
            <div className="movies-slider">
              <h2 class="fw-bold">Sci-Fi TV <Link to="/Tv"><span><i class="bi bi-arrow-right-circle-fill"></i></span></Link></h2>

              <Slider {...settings}>

                {tv.map((item) => {
                  const { poster_path, title, _id } = item
                  return (
                    <div key={_id} className="movie-card">

                      <Link to={`/trendingdetails/${_id}`}>
                        <img src={poster_path} alt="" class="img-fluid slideimg" />
                        <h5>{title}</h5>
                      </Link>
                    </div>

                  )
                })}
              </Slider>

            </div>

          </div>
          <div class="container-fluid mt-5">
            <div className="movies-slider">
              <h2 class="fw-bold">Docuseries <Link to="/Docuseries"><span><i class="bi bi-arrow-right-circle-fill"></i></span></Link></h2>

              <Slider {...settings}>

                {docuseries.map((item) => {
                  const { poster_path, title, _id } = item
                  return (
                    <div key={_id} className="movie-card">

                      <Link to={`/trendingdetails/${_id}`}>
                        <img src={poster_path} alt="" class="img-fluid slideimg" />
                        <h5>{title}</h5>
                      </Link>
                    </div>

                  )
                })}
              </Slider>

            </div>

          </div>
        </div>
      </div>
    </div>
  )
}
export default Home;